<template>
  <div class="min-h-screen p-6">
    <div class="max-w-4xl mx-auto">
      <!-- Header -->
      <div class="flex items-center gap-4 mb-8"
           v-motion
           :initial="{ opacity: 0, y: -20 }"
           :enter="{ opacity: 1, y: 0, transition: { duration: 500 } }">
        <button
          @click="$emit('close-settings')"
          class="p-3 rounded-xl transition-all duration-200 hover:scale-110"
          :class="getButtonClass()">
          <svg class="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M15 19l-7-7 7-7" />
          </svg>
        </button>
        <h1 class="text-3xl font-bold" :class="getTextClass()">
          <span class="text-4xl mr-2"></span>
          Ustawienia
        </h1>
      </div>

      <!-- Theme Selection -->
      <div class="mb-8"
           v-motion
           :initial="{ opacity: 0, y: 20 }"
           :enter="{ opacity: 1, y: 0, transition: { duration: 500, delay: 100 } }">
        <div class="backdrop-blur-lg rounded-2xl p-6 shadow-xl" :class="getCardClass()">
          <div class="flex items-center gap-3 mb-6">
            <span class="text-3xl"></span>
            <h2 class="text-2xl font-bold" :class="getTextClass()">Motywy</h2>
          </div>

          <div class="grid grid-cols-1 md:grid-cols-3 gap-4">
            <div
              v-for="(theme, key) in themes"
              :key="key"
              @click="changeTheme(key)"
              class="relative group cursor-pointer"
              v-motion
              :initial="{ opacity: 0, scale: 0.9 }"
              :enter="{ opacity: 1, scale: 1, transition: { duration: 300 } }">
              <!-- Theme Card -->
              <div
                class="relative overflow-hidden rounded-xl border-2 transition-all duration-300 p-6"
                :class="currentTheme === key ? getActiveThemeClass(key) : getInactiveThemeClass()">
                <!-- Theme Icon -->
                <div class="text-6xl mb-4 text-center animate-float">
                  {{ theme.icon }}
                </div>

                <!-- Theme Name -->
                <h3 class="text-lg font-bold text-center mb-2" :class="getThemeTextClass(key)">
                  {{ theme.name }}
                </h3>

                <!-- Theme Description -->
                <p class="text-sm text-center" :class="getThemeMutedTextClass(key)">
                  {{ theme.description }}
                </p>

                <!-- Selected Badge -->
                <div v-if="currentTheme === key"
                     class="absolute top-3 right-3 w-8 h-8 rounded-full flex items-center justify-center shadow-lg"
                     :class="getSelectedBadgeClass(key)">
                  <svg class="w-5 h-5 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path stroke-linecap="round" stroke-linejoin="round" stroke-width="3" d="M5 13l4 4L19 7" />
                  </svg>
                </div>

                <!-- Feminine Theme Decorations -->
                <div v-if="key === 'feminine'" class="absolute inset-0 pointer-events-none overflow-hidden opacity-20">
                  <span class="absolute top-2 left-2 text-3xl animate-float"></span>
                  <span class="absolute bottom-2 right-2 text-3xl" style="animation: float 3s ease-in-out 0.5s infinite"></span>
                  <span class="absolute top-1/2 right-4 text-2xl" style="animation: float 3s ease-in-out 1s infinite"></span>
                </div>

                <!-- Hover Effect -->
                <div class="absolute inset-0 bg-gradient-to-br opacity-0 group-hover:opacity-10 transition-opacity duration-300"
                     :class="getHoverGradient(key)"></div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <!-- Text Size -->
      <div class="mb-8"
           v-motion
           :initial="{ opacity: 0, y: 20 }"
           :enter="{ opacity: 1, y: 0, transition: { duration: 500, delay: 200 } }">
        <div class="backdrop-blur-lg rounded-2xl p-6 shadow-xl" :class="getCardClass()">
          <div class="flex items-center gap-3 mb-6">
            <span class="text-3xl"></span>
            <h2 class="text-2xl font-bold" :class="getTextClass()">Rozmiar tekstu</h2>
          </div>

          <div class="flex gap-3 mb-4">
            <button
              v-for="size in textSizes"
              :key="size.value"
              @click="changeTextSize(size.value)"
              class="flex-1 px-6 py-3 rounded-xl font-semibold transition-all duration-200 hover:scale-105"
              :class="currentTextSize === size.value ? getActiveButtonClass() : getInactiveButtonClass()">
              {{ size.label }}
            </button>
          </div>

          <div class="p-6 rounded-xl" :class="getPreviewClass()">
            <p :class="['transition-all duration-200', getTextSizeClass(currentTextSize)]">
              Przykadowy tekst wiadomoci. Tak bd wyglda wiadomoci na chacie.
            </p>
          </div>
        </div>
      </div>

      <!-- Avatar Selection -->
      <div class="mb-8"
           v-motion
           :initial="{ opacity: 0, y: 20 }"
           :enter="{ opacity: 1, y: 0, transition: { duration: 500, delay: 300 } }">
        <div class="backdrop-blur-lg rounded-2xl p-6 shadow-xl" :class="getCardClass()">
          <div class="flex items-center gap-3 mb-6">
            <span class="text-3xl"></span>
            <h2 class="text-2xl font-bold" :class="getTextClass()">Wybierz Avatar</h2>
          </div>

          <div class="grid grid-cols-6 sm:grid-cols-8 md:grid-cols-12 gap-3">
            <button
              v-for="(avatar, index) in avatars"
              :key="avatar"
              @click="changeAvatar(avatar)"
              v-motion
              :initial="{ opacity: 0, scale: 0 }"
              :enter="{ opacity: 1, scale: 1, transition: { duration: 200, delay: index * 20 } }"
              class="relative aspect-square rounded-xl text-3xl flex items-center justify-center transition-all duration-200 hover:scale-110"
              :class="currentAvatar === avatar ? getActiveAvatarClass() : getInactiveAvatarClass()">
              {{ avatar }}
              <div v-if="currentAvatar === avatar"
                   class="absolute -top-1 -right-1 w-6 h-6 rounded-full flex items-center justify-center shadow-lg"
                   :class="getSelectedBadgeClass(currentTheme)">
                <span class="text-xs text-white"></span>
              </div>
            </button>
          </div>
        </div>
      </div>

      <!-- User Info -->
      <div v-motion
           :initial="{ opacity: 0, y: 20 }"
           :enter="{ opacity: 1, y: 0, transition: { duration: 500, delay: 400 } }">
        <div class="backdrop-blur-lg rounded-2xl p-6 shadow-xl" :class="getCardClass()">
          <div class="flex items-center gap-3 mb-6">
            <span class="text-3xl"></span>
            <h2 class="text-2xl font-bold" :class="getTextClass()">Informacje o koncie</h2>
          </div>

          <div class="space-y-4">
            <div class="flex items-center gap-4 p-4 rounded-xl" :class="getPreviewClass()">
              <div class="w-16 h-16 rounded-full bg-gradient-to-br from-blue-500 to-purple-600 flex items-center justify-center text-3xl shadow-lg">
                {{ currentAvatar }}
              </div>
              <div class="flex-1">
                <p class="font-semibold text-lg" :class="getTextClass()">{{ currentUser?.username }}</p>
                <p class="text-sm" :class="getTextMutedClass()">{{ currentUser?.email }}</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { themes, avatars, applyTheme, getTextSizeClass } from '../themes.js'

export default {
  name: 'Settings',
  props: {
    currentUser: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      themes,
      avatars,
      currentTheme: localStorage.getItem('theme') || 'darkModern',
      currentTextSize: localStorage.getItem('textSize') || 'medium',
      currentAvatar: localStorage.getItem('userAvatar') || '',
      textSizes: [
        { value: 'small', label: 'May' },
        { value: 'medium', label: 'redni' },
        { value: 'large', label: 'Duy' }
      ]
    }
  },
  methods: {
    changeTheme(themeName) {
      this.currentTheme = themeName
      localStorage.setItem('theme', themeName)
      applyTheme(themeName)
      this.$emit('theme-changed', themeName)
    },
    changeTextSize(size) {
      this.currentTextSize = size
      localStorage.setItem('textSize', size)
      this.$emit('text-size-changed', size)
    },
    changeAvatar(avatar) {
      this.currentAvatar = avatar
      localStorage.setItem('userAvatar', avatar)
      this.$emit('avatar-changed', avatar)
    },
    getTextSizeClass,
    // Theme-aware styling methods
    getCardClass() {
      return this.currentTheme === 'feminine' ? 'bg-white/90 border border-pink-200' :
             this.currentTheme === 'lightModern' ? 'bg-white/90 border border-gray-200' :
             'bg-slate-800/90 border border-slate-700'
    },
    getTextClass() {
      return this.currentTheme === 'feminine' ? 'text-pink-900' :
             this.currentTheme === 'lightModern' ? 'text-gray-900' :
             'text-slate-100'
    },
    getTextMutedClass() {
      return this.currentTheme === 'feminine' ? 'text-pink-600' :
             this.currentTheme === 'lightModern' ? 'text-gray-600' :
             'text-slate-400'
    },
    getButtonClass() {
      return this.currentTheme === 'feminine' ? 'bg-pink-100 hover:bg-pink-200 text-pink-700' :
             this.currentTheme === 'lightModern' ? 'bg-gray-100 hover:bg-gray-200 text-gray-700' :
             'bg-slate-700 hover:bg-slate-600 text-slate-300'
    },
    getActiveButtonClass() {
      return this.currentTheme === 'feminine' ? 'bg-gradient-to-r from-pink-500 to-rose-500 text-white shadow-lg' :
             'bg-gradient-to-r from-blue-500 to-indigo-500 text-white shadow-lg'
    },
    getInactiveButtonClass() {
      return this.currentTheme === 'feminine' ? 'bg-pink-50 text-pink-700 hover:bg-pink-100' :
             this.currentTheme === 'lightModern' ? 'bg-gray-100 text-gray-700 hover:bg-gray-200' :
             'bg-slate-700 text-slate-300 hover:bg-slate-600'
    },
    getPreviewClass() {
      return this.currentTheme === 'feminine' ? 'bg-pink-50/50' :
             this.currentTheme === 'lightModern' ? 'bg-gray-50/50' :
             'bg-slate-900/50'
    },
    getActiveThemeClass(themeKey) {
      if (themeKey === 'feminine') {
        return 'border-pink-500 bg-gradient-to-br from-pink-50 to-rose-50 shadow-xl shadow-pink-500/30'
      } else if (themeKey === 'lightModern') {
        return 'border-blue-500 bg-gradient-to-br from-blue-50 to-indigo-50 shadow-xl shadow-blue-500/30'
      } else {
        return 'border-blue-500 bg-gradient-to-br from-slate-800 to-slate-900 shadow-xl shadow-blue-500/30'
      }
    },
    getInactiveThemeClass() {
      return this.currentTheme === 'feminine' ? 'border-pink-200 bg-white/50 hover:bg-pink-50/50' :
             this.currentTheme === 'lightModern' ? 'border-gray-200 bg-white/50 hover:bg-gray-50/50' :
             'border-slate-700 bg-slate-800/50 hover:bg-slate-700/50'
    },
    getThemeTextClass(themeKey) {
      if (themeKey === 'feminine') return 'text-pink-700'
      if (themeKey === 'lightModern') return 'text-gray-900'
      return 'text-slate-100'
    },
    getThemeMutedTextClass(themeKey) {
      if (themeKey === 'feminine') return 'text-pink-500'
      if (themeKey === 'lightModern') return 'text-gray-600'
      return 'text-slate-400'
    },
    getSelectedBadgeClass(themeKey) {
      if (themeKey === 'feminine') return 'bg-gradient-to-br from-pink-500 to-rose-500'
      return 'bg-gradient-to-br from-blue-500 to-indigo-500'
    },
    getHoverGradient(themeKey) {
      if (themeKey === 'feminine') return 'from-pink-500 to-rose-500'
      return 'from-blue-500 to-indigo-500'
    },
    getActiveAvatarClass() {
      return this.currentTheme === 'feminine' ? 'bg-gradient-to-br from-pink-500 to-rose-500 shadow-lg shadow-pink-500/50' :
             'bg-gradient-to-br from-blue-500 to-indigo-500 shadow-lg shadow-blue-500/50'
    },
    getInactiveAvatarClass() {
      return this.currentTheme === 'feminine' ? 'bg-pink-100 hover:bg-pink-200' :
             this.currentTheme === 'lightModern' ? 'bg-gray-100 hover:bg-gray-200' :
             'bg-slate-700 hover:bg-slate-600'
    }
  }
}
</script>
